* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.radiogroup {
  border: none;
  margin-top: 30px;
  margin-right: 30px;
  position: relative;
}
.radiogroup .first {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(90deg, #5a5551, #0f2c34);
  z-index: 0;
  width: 30px;
  height: 30px;
  color: var(--li-bg);
  right: 82px;
  border: 0.5px solid rgba(0, 0, 0, 0.521);
}
.radiogroup .second {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(to right, #d0f1f5 40%, rgba(255, 255, 255, 0.8549019608));
  z-index: 0;
  width: 30px;
  height: 30px;
  color: var(--li-bg);
  right: 44px;
  border: 0.1px solid rgba(0, 0, 0, 0.521);
}
.radiogroup .third {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(to left, #0a2c30, hsl(0, 0%, 0%));
  z-index: 0;
  width: 30px;
  height: 30px;
  color: var(--li-bg);
  right: 5px;
  border: 0.5px solid rgba(0, 0, 0, 0.521);
}
.radiogroup input[type=radio] {
  height: 30px;
  width: 30px;
  margin-right: 5px;
  position: relative;
  z-index: 1;
  opacity: 0;
}

.container {
  display: flex;
  justify-content: flex-end;
  margin-right: 40px;
}

.button {
  content: "";
  background: black;
  height: 30px;
  width: 30px;
}

body {
  background: var(--bg);
  min-height: 100vh;
  text-align: center;
}

.heading {
  color: var(--big-t);
  font-family: sans-serif;
  font-size: 72px; /* Large and bold display */
  font-weight: 300; /* Light weight */
  letter-spacing: 1px; /* Slight spacing for elegance */
  margin-bottom: 48px;
}

.text {
  font-size: 16px;
  padding: 12px 16px;
  border-radius: 15px 0 0 15px;
  height: 40px;
  background: var(--input);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: var(--input-t);
  margin-right: 0;
  border: none;
  min-width: 35vw;
}

.text:hover, .text:focus {
  filter: brightness(140%);
}

.igot {
  background: var(--button-bg);
  color: var(--button-t);
  margin-left: 0;
  height: 40px;
  border-radius: 0 15px 15px 0;
  border: none;
  min-width: 8vw;
}

.igot:hover {
  filter: brightness(110%);
}

.date {
  margin-top: 30px;
  color: var(--date);
  font-family: sans-serif;
  font-size: 14px;
  margin-bottom: 20px;
}

li {
  list-style-type: none;
  color: var(--big-t);
  min-height: 48px;
  background-color: var(--li-bg);
  font-family: sans-serif;
  padding: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 25px;
  min-width: 25vw;
  margin-bottom: 15px;
  zoom: 1;
  overflow: hidden;
  word-break: break-word;
}

.buttons {
  display: flex;
  justify-content: center;
  position: relative;
  text-align: center;
}
.buttons .tick {
  width: 35px;
  height: 35px;
  z-index: 3;
  opacity: 0;
}
.buttons .delete {
  width: 35px;
  height: 35px;
  z-index: 3;
  margin-left: 10px;
  opacity: 0;
}
.buttons .left {
  position: absolute;
  border-radius: 50%;
  background-color: var(--button-bg);
  z-index: 2;
  width: 35px;
  height: 35px;
  left: 35px;
  margin-left: 10px;
  color: var(--li-button);
  font-weight: 700;
  font-size: 20px;
  place-content: center;
}
.buttons .right {
  position: absolute;
  border-radius: 50%;
  background-color: var(--button-bg);
  z-index: 2;
  width: 35px;
  height: 35px;
  right: 35px;
  margin-right: 10px;
  color: var(--li-button);
  font-weight: 700;
  font-size: 20px;
  place-content: center;
}
.buttons .tick:hover ~ .right {
  filter: brightness(110%);
}
.buttons .delete:hover ~ .left {
  filter: brightness(110%);
}

.textfield {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

input[type=radio] {
  cursor: pointer;
}

:root {
  --body-bg-teal:linear-gradient(90deg, #7a6a55, #12344f);
  --big-text:#f8d8d8;
  --input-bg:#1a1a1a;
  --input-text:#cccccc;
  --button-back:#f8d8d8;
  --button-text:#333333;
  --li:hsla(0, 0%, 10%,0.5);
  --date:var(--big-t);
  --bg:var(--body-bg-teal);
  --big-t:var(--big-text);
  --input:var(--input-bg);
  --input-t:var(--input-text);
  --button-bg:var(--button-back);
  --button-t:var(--button-text);
  --li-bg:var(--li);
  --li-button:var(--input-bg);
}

main {
  justify-items: center;
  align-content: center;
  margin-top: 20px;
}

body.dark {
  --bg:linear-gradient(to left,#0a2c30,hsla(0, 0%, 0%,1));
  --big-t:white;
  --input:#0a3749;
  --input-t:lightgray;
  --button-bg:#000f11;
  --button-t:white;
  --li-bg:#0b3e44;
  --date:#cccccc;
  --li-button:white;
}
body.dark li {
  color: #cccccc;
}
body.dark .tick:hover ~ .right {
  filter: brightness(200%);
}
body.dark .delete:hover ~ .left {
  filter: brightness(200%);
}
body.dark .igot:hover {
  filter: brightness(150%);
}

body.light {
  --bg:linear-gradient(to right,#d0f1f5 40%,hsl(0, 0%, 95%));
  --big-t:black;
  --input:#D3D3D3;
  --input-t:#4F4F4F;
  --button-bg:#ffffffe0;
  --button-t:#4F4F4F;
  --li-bg:#d3d3d3;
  --date:#666666;
  --li-button:black;
}
body.light li {
  color: #333333;
}
body.light .tick:hover ~ .right {
  filter: brightness(120%);
}
body.light .delete:hover ~ .left {
  filter: brightness(120%);
}
body.light .igot:hover {
  filter: brightness(120%);
}

body.teal {
  --body-bg-teal:linear-gradient(90deg, #7a6a55, #12344f);
  --big-text:#f8d8d8;
  --input-bg:#1a1a1a;
  --input-text:#cccccc;
  --button-back:#f8d8d8;
  --button-text:#333333;
  --li:hsla(0, 0%, 10%,0.5);
  --date:var(--big-t);
  --bg:var(--body-bg-teal);
  --big-t:var(--big-text);
  --input:var(--input-bg);
  --input-t:var(--input-text);
  --button-bg:var(--button-back);
  --button-t:var(--button-text);
  --li-bg:var(--li);
  --li-button:var(--input-bg);
}

@media (min-width: 0px) {
  li {
    list-style-type: none;
    color: var(--big-t);
    min-height: 48px;
    background-color: var(--li-bg);
    font-family: sans-serif;
    padding: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 25px;
    min-width: 80vw;
    margin-bottom: 15px;
    zoom: 1;
  }
  .text {
    min-width: 70vw;
  }
  .igot {
    min-width: 20vw;
  }
  main {
    margin-top: 10vh;
  }
  .radiogroup {
    margin-right: 0px;
  }
}
@media (min-width: 600px) {
  .text {
    font-size: 16px;
    padding: 12px 16px;
    border-radius: 15px 0 0 15px;
    height: 40px;
    background: var(--input);
