    color: var(--input-t);
    margin-right: 0;
    border: none;
    min-width: 50vw;
  }
  .igot {
    background: var(--button-bg);
    color: var(--button-t);
    margin-left: 0;
    height: 40px;
    border-radius: 0 15px 15px 0;
    border: none;
    min-width: 15vw;
  }
  li {
    list-style-type: none;
    color: var(--big-t);
    min-height: 48px;
    background-color: var(--li-bg);
    font-family: sans-serif;
    padding: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 25px;
    min-width: 50vw;
    margin-bottom: 15px;
    zoom: 1;
  }
  main {
    margin-top: 10vh;
  }
  .radiogroup {
    margin-right: 0px;
  }
}
@media (min-width: 1050px) {
  li {
    list-style-type: none;
    color: var(--big-t);
    min-height: 48px;
    background-color: var(--li-bg);
    font-family: sans-serif;
    padding: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 25px;
    min-width: 30vw;
    margin-bottom: 15px;
    zoom: 1;
  }
  .text {
    min-width: 40vw;
  }
  .igot {
    min-width: 8vw;
  }
  main {
    margin-top: 10vh;
  }
  .radiogroup {
    margin-right: 0px;
  }
}
.strike {
  text-decoration: line-through;
  opacity: 0.5;
}